.Page1 {
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-size: cover;
}

.part1-Page1 {
    width: 90%;
    margin: 0 auto;
    background-color: aqua;
    height: 40vh;
    font-size: small;
    font-weight: 600;
}

.part2-Page1 {
    overflow-y: scroll;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    top: 20px;
}

.part2-Page1 div {
    width: 47%;
    height: 100px;
    max-height: 100px;
    display: flex;
    flex-direction: row-reverse;
    margin: 5px auto;
    border-radius: 0px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}

.battery-Page1,
.active-Page1,
.ConnectTel-Page1,
.alarm-Page1,
.notif-Page1,
.connect-Page1 {
    background-color: #1a1a1a;
    color: #fff;
}

.part2-Page1 div div {
    border: none;
}

.part2-Page1 div div {
    /* background-color: #d7ecff00; */
    padding: 0px;
    margin: 0px;
    width: auto;
    border-radius: 0px;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    -ms-border-radius: 0px;
    -o-border-radius: 0px;
    border: none;
}

.part2-Page1 div .text-Page1 {
    padding: 7px;
    padding-right: 15px;
    font-size: small;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    height: fit-content;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

.part2-Page1 div .text-Page1 :nth-child(2) {
    height: fit-content;
    top: 0px !important;
    /* border: 2pt solid; */
    width: 110%;
    padding: 0px;
    font-weight: 300;
}

.part2-Page1 div .text-Page1 .title-Page1 {
    height: fit-content;
}

.part2-Page1 div .text-Page1 .title-Page1 p {
    font-size: small;
    font-weight: 600;
}

.part2-Page1 div .icons-Page1 {
    padding: 5px;
    padding-right: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.part2-Page1 div .icons-Page1 .icon-Page1 {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1pt solid #0afc96;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.part2-Page1 div .icons-Page1 #icon1-Page {
    /* background-image: url(https://s25.picofile.com/file/8452901176/unlock_5_.png); */
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
}

